import {useEffect, useState} from "react";
import {adaptive} from "../interfaces/enums";


const useGetAdaptive = () => {

    const [windowSize, setWindowSize] = useState<{width: number, height: number}>({
        width: 0,
        height: 0,
    });

    useEffect(() => {
        // Handler to call on window resize
        function handleResize() {
            // Set window width/height to state
            setWindowSize({
                width: window.innerWidth,
                height: window.innerHeight,
            });
        }
        // Add event listener
        window.addEventListener("resize", handleResize);
        // Call handler right away so state gets updated with initial window size
        handleResize();
        // Remove event listener on cleanup
        return () => window.removeEventListener("resize", handleResize);
    }, []); // Empty array ensures that effect is only run on mount

    if (windowSize.width > 2560) {
        return adaptive.TWOK
    }  else if (windowSize.width > 1920) {
        return adaptive.FULLHD
    } else if (windowSize.width > 1280) {
        return adaptive.HD
    }  else if (windowSize.width > 720) {
        return adaptive.MOBILE
    }
}

export default useGetAdaptive